@import url(https://fonts.googleapis.com/css2?family=Lato&family=Raleway:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.hobby-container{width:100%;display:grid;place-items:center;padding-bottom:3em}.hobby-container .hobby-window{background:ivory;padding:20px;display:grid;grid-template-rows:repeat(1fr, 12);grid-template-columns:repeat(1fr, 12);border-radius:15px;max-width:50%}.hobby-container .hobby-window h1{text-align:center}.hobby-container .hobby-window *{padding:10px}.hobby-container .hobby-window .input-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.hobby-container .hobby-window .input-container label{width:30%;font-size:1.15rem}.hobby-container .hobby-window .input-container input,.hobby-container .hobby-window .input-container textarea{-webkit-flex-grow:1;flex-grow:1;border:lightgrey solid 1px;outline:none;max-width:70%}.hobby-container .hobby-window .checks{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.hobby-container .hobby-window .submit-btn{border:none;background:#54A4A4;color:ivory;font-size:1.15rem;box-shadow:lightgrey 1px 1px 5px 2px}.hobby-container .hobby-window .submit-btn:hover{background:#438282}

.navbar{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;width:100vw}.navbar a{text-decoration:none;padding:10px;border-radius:15px;color:ivory}.navbar a:hover{background:lightgrey}.navbar .nav-left{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.navbar .nav-left img{border-radius:50%;cursor:pointer;z-index:1000}.navbar .nav-left *{margin:20px}.navbar .nav-right *{margin:20px}

.task-view-container{display:grid;place-items:center;height:100%;width:100%}.task-view-container .task-view{background:ivory;padding:30px;border-radius:15px}.task-view-container .task-view>*{padding:10px 0;text-align:center}.task-view-container .task-view .header h1{font-size:2.5rem}.task-view-container .task-view .task-content{display:-webkit-flex;display:flex}.task-view-container .task-view .task-content .task-left,.task-view-container .task-view .task-content .task-right{padding:20px;border:lightgrey solid 1px;border-radius:3px;-webkit-flex-basis:0;flex-basis:0;-webkit-flex-grow:1;flex-grow:1;text-align:left;font-size:1.25rem}.task-view-container .task-view .task-content .task-left h2,.task-view-container .task-view .task-content .task-right h2{font-size:2.15rem}.task-view-container .task-view .task-content .task-left p,.task-view-container .task-view .task-content .task-right p{font-size:0.75rem;word-wrap:normal;max-width:60%;color:gray}.task-view-container .task-view .task-content .task-left .checkbox-div>*,.task-view-container .task-view .task-content .task-right .checkbox-div>*{padding:0 5px}.task-view-container .task-view .task-content .task-left input,.task-view-container .task-view .task-content .task-right input{max-width:75%;margin-left:2px}.task-view-container .task-view .task-content .task-left .btn,.task-view-container .task-view .task-content .task-right .btn{margin-top:5px;font-size:1.15rem}.task-view-container .task-view .button-container{display:-webkit-flex;display:flex;width:100%;font-size:1.25rem}.task-view-container .task-view .button-container button{-webkit-flex-grow:1;flex-grow:1;border:none;outline:none;padding:5px;color:ivory;border:ivory solid 2px;border-radius:15px;box-shadow:lightgrey 2px 2px 2px 2px;cursor:pointer}.task-view-container .task-view .button-container button.ready{background:#54a4a4}.task-view-container .task-view .button-container button.unready{background:#a45454}.task-view-container .task-view .text-chat{text-align:left;font-size:1.15rem}.task-view-container .task-view .text-chat *{margin:1px 0}.task-view-container .task-view .text-chat input{outline:none;border:lightgrey 2px solid}.task-view-container .task-view .text-chat button{border:none;outline:none;background:#54a4a4;color:ivory;border:#54a4a4 solid 2px;box-shadow:lightgrey 2px 2px 2px 2px;cursor:pointer}.none{display:none}

.home-container{width:100%;padding:30px 30px 0 30px;color:#FFFFFF}.home-container h1{font-family:'Raleway', sans-serif;font-size:96px}.home-container p{font-size:20px}.home-container .image{float:left;width:50%;-webkit-transform:translate(-1em, -12em);transform:translate(-1em, -12em)}.home-container .content{float:right;width:50%;-webkit-transform:translate(0, -2em);transform:translate(0, -2em)}.home-container .homepage-img{height:100vh;object-fit:contain}.home-container .btn-main{display:inline-block;background:0%;color:#fff;border:3px solid #FFF;padding:15px 70px;margin:15px 5px;border-radius:0px;cursor:pointer;text-decoration:none;font-size:inherit;font-family:inherit}.home-container .btn-main:hover{border:3px solid #000000;color:#000000}

#root{height:100%;background:#54A5A5}.display{background:#54A5A5}

.base-container{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.base-container .header{font-size:24px;font-family:"Lato", sans-serif}.base-container .content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.base-container .content .image{width:21em}.base-container .content .image img{width:100%;height:100%}.base-container .content .form{margin-top:2em;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.base-container .content .form .form-group{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.base-container .content .form .form-group label{font-size:20px}.base-container .content .form .form-group input{margin-top:6px;min-width:18em;height:37px;padding:0px 10px;font-size:16px;font-family:"Lato", sans-serif;background-color:#f3f3f3;border:0;border-radius:4px;margin-bottom:31px;transition:all 250ms ease-in-out}.base-container .content .form .form-group input:focus{outline:none;box-shadow:0px 0px 12px 0.8px #3474dbb2}.base-container .footer{margin-top:3em}

.App{text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:"Lato", sans-serif;padding-bottom:3em}.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.login{width:27em;height:40em;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-top:5px;position:relative;z-index:99}.login .container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#fff;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);border-radius:4px;position:relative;z-index:99;width:100%;height:100%;z-index:99;padding:17px 10px}.login .right-side{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;height:90%;background-color:#3498db;width:100%;position:absolute;right:-34%;border-radius:6px;z-index:1;transition:all 400ms ease-in-out;cursor:pointer;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.281)}.login .right-side.right{right:-40%;-webkit-align-items:flex-end;align-items:flex-end}.login .right-side.right:hover{right:-45%}.login .right-side.left{right:40%;-webkit-align-items:flex-start;align-items:flex-start}.login .right-side.left:hover{right:45%}.login .right-side .text{font-size:21px;font-weight:500;color:#fff;margin-right:3em;margin-left:3em}.btn{font-size:21px;padding:5px 20px;border:0;background-color:#3498db;color:#fff;border-radius:3px;transition:all 250ms ease-in-out;cursor:pointer}.btn:hover{background-color:#2386c8}.btn:focus{outline:none}

